* {
  box-sizing: border-box;
  font-family: $font-open;
}

.container {
  @media #{$medium-and-up} {
    width: 90%;
  }
  @media #{$large-and-up} {
    width: 85%;
  }
}

.flex {
  display: flex;
}

.flex-center {
  align-items: center;
}

.flex-wrap {
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.space-evenly {
  justify-content: space-evenly;
}

.mega-link.mega-link {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 5;
}

strong {
  font-weight: 700;
}

img {
  max-width: 100%;
}

@mixin cover {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

section.alternating {
  padding: 2em 0;

  &.n-0 {
    background-image: url('../images/paper-fibers.png');
    background-repeat: repeat;
  }
}

section.container, .section.padding {
  padding: 2.5em 0;
}

.background-paper {
  background-color: #E5E6E6;
  background-image: url('../images/paper-fibers.png');
  background-repeat: repeat;
  padding: 3em 0;
}



